<template>
  <div class="group swiper-container">
    <div class="swiper-wrapper">
      <div class="group-info swiper-slide">
        <div class="group-img">
        <img src="../assets/group/web.png"/>
        </div>
        <br />
        <h1>Web</h1>
        <p>Web</p><p></p><p>Web App</p>
      </div>
      <div class="group-info swiper-slide">
        <div class="group-img">
        <img src="../assets/group/lab.png"/>
          </div>
        <br />
        <h1>Lab</h1>
        <p>LabGeek</p><p></p><p></p>
      </div>
      <div class="group-info swiper-slide">
        <div class="group-img">
        <img src="../assets/group/design.png"/>
          </div>
        <br />
        <h1>Design</h1>
        <p>DesignWebApp</p><p></p>
      </div>
      <div class="group-info swiper-slide">
        <div class="group-img">
        <img src="../assets/group/pm.png"/>
          </div>
        <br />
        <h1>PM</h1>
        <p>PM</p><p>UI</p>
      </div>
      <div class="group-info swiper-slide">
        <div class="group-img">
        <img src="../assets/group/android.png"/>
          </div>
        <br />
        <h1>Android</h1>
        <p>AndroidAndroid.</p><p></p><p>App</p>
      </div>
      <div class="group-info swiper-slide">
        <div class="group-img">
        <img src="../assets/group/ios.png"/>
          </div>
        <br />
        <h1>iOS</h1>
        <p>iOS</p><p></p><p></p><p>Stay Hungry, Stay Foolish.</p>
      </div>
      <div class="group-info swiper-slide">
        <div class="group-img">
        <img src="../assets/group/game.png"/>
          </div>
        <br />
        <h1>Game</h1>
        <p>Game</p><p></p><p></p><p></p>
      </div>
    </div>
    <div class="swiper-pagination"></div>
  </div>
</template>

<script>

export default {
  data () {
    return {
    }
  },
  ready(){
    var nameMap={
      'web':0,
      'lab':1,
      'design':2,
      'pm':3,
      'android':4,
      'ios':5,
      'game':6
    };

    var num=location.hash.split('/')[2];
    num=nameMap[num];
    num=parseInt(num);
    var numMap={
      0:'web',
      1:'lab',
      2:'design',
      3:'pm',
      4:'android',
      5:'ios',
      6:'game'
    };
    new Swiper('.swiper-container', {
      height: 600,
      pagination : '.swiper-pagination',
      initialSlide :num,
      onTransitionEnd: function(swiper){
        window.location='#!/group/'+numMap[swiper.activeIndex];
      }
    })
  },
  name:'group'
}
</script>

<style scoped>

  .group{
    width: 100vw !important;
    margin-top: 20px;
  }

  .group-img{
    height: 260px;
  }

  .group-info{
    width: 100vw;
    display: list-item;
    text-align: center;
  }

  .group-info img{
    width: 90vw;
    max-width: 400px;
    height: auto;
    margin-right: auto;
    margin-left: 5vw;
  }

  .group-info h1{
    color: #00ffb8;
    font-weight: lighter;
    height: 30px;
    margin-left: 5vw;
    text-align: left;
    margin-bottom: 15px;
    font-size: 20px;
  }

  .group-info p{
    width: 300px;
    color: white;
    margin-left: 5vw;
    text-align: left;
    text-indent: 2em;
    font-size: 16px;
  }

</style>



